@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{box-sizing:border-box;padding:0;margin:0;color:#f6f6f6}body#body{background-color:#212428;font-family:"Montserrat",sans-serif;overflow:hidden}p,pre,article,a{font-size:16px;text-decoration:none}.p_color{color:#ff014f}ul#mainNav{width:100%;justify-content:end;direction:ltr}#MenuD nav.navbar a{text-transform:uppercase;font-weight:300}header#MenuM{display:none}header#MenuM>section.breadcum{border:1px solid;padding:5px 4px 0px 4px;border-radius:2px;transition:all .3s}header#MenuM>section.breadcum:focus,header#MenuM>section.breadcum:hover{background-color:#f6f6f6}header#MenuM>section.breadcum:focus span.menu_line,header#MenuM>section.breadcum:hover span.menu_line{background-color:#212428;transition:all .3s}header#MenuM>section.breadcum:focus pre.menu_text,header#MenuM>section.breadcum:hover pre.menu_text{color:#212428;transition:all .3s}header#MenuM a.mobile_menu{text-decoration:none}header#MenuM .menu_text{color:#fff;font-size:9px;margin:0px;margin-bottom:2px;text-align:center}header#MenuM span.menu_line{display:block;width:26px;background:#fff;height:1px;margin:auto;margin-bottom:4px}ul.hero_icons{padding:0;margin:20px 0 0 0}ul.hero_icons li{list-style-type:none;display:inline;margin-right:20px;width:100%}ul.hero_icons li a{background:linear-gradient(145deg, #1e2024, #23272b);border-radius:6px;box-shadow:10px 10px 19px #1c1e22,-10px -10px 19px #262a2e;display:inline-block;font-size:14px;height:60px;letter-spacing:2px;line-height:60px;position:relative;text-align:center;text-transform:uppercase;transition:.4s;width:60px;cursor:pointer}ul.hero_icons li a:hover{background:linear-gradient(to right bottom, #212428, #16181c);transition:.4s}@media only screen and (max-width: 992px){main#MenuDekstop{display:none}header#MenuM{display:block;float:right}}
